.search-form-container {
  background-color: #5074e9;
  padding: 10px;
}

.search-form {
  justify-content: center;
  gap: 5px;
  display: flex;
}

.search-input {
  height: 30px;
  border-radius: 5px;
  padding: 5px;
  font-size: 24px;
}

.search-input::-ms-input-placeholder {
  font-size: 18px;
}

